// Services
import CartService from 'Services/b8one.cartService.js';
import VtexCatalogService from 'Services/b8one.vtexCatalogService.js';

// Utils
import SKUS_TO_IGNORE from 'Utils/skusToIgnore';

export default class StorageService {
  constructor() {
    this.cartService = new CartService();
    this.vtexCatalogService = new VtexCatalogService();
  }

  async getTotalStorageQuantity() {
    const { items } = await this.cartService.getCurrent();

    const productsWeightArray = await Promise.all(
      items
        .filter(product => !SKUS_TO_IGNORE.includes(parseInt(product.id)))
        .map(async ({ productId, id, quantity }) => {
          const { data } = await this.vtexCatalogService.getProductVariations(
            productId
          );

          const skuSelected = data.skus.filter(
            ({ sku }) => parseInt(sku) === parseInt(id)
          )[0];

          if (skuSelected) {
            const { weight } = skuSelected.measures;
            const total = weight * quantity;

            return total;
          }

          return 0;
        })
    );

    const totalWeight = Math.round(
      productsWeightArray.reduce((total, weight) => total + weight, 0)
    );

    let storageQuantity = Math.round(totalWeight / 5);

    storageQuantity = storageQuantity > 0 ? storageQuantity : 1;

    return storageQuantity;
  }

  async addSKUStorageToOrderForm(storageSKUID, quantity) {
    return await this.cartService.add(storageSKUID, quantity);
  }

  async removeSKUStorageInOrderForm(storageSKUID, products) {
    const index = products.findIndex(
      product => parseInt(product.id) === storageSKUID
    );

    if (index !== -1) {
      return await this.cartService.remove(index);
    }
  }

  async dispatchActionWorksWithStorageSKU(userSelectedStorageMonths = 1) {
    const storageSKUID = 33;

    const { customData, items } = await this.cartService.getCurrent();

    if (!customData) return;

    const orderInfo = customData.customApps.filter(
      customApp => customApp.id === 'orderinfo'
    )[0];

    const storageProduct = items.filter(product => {
      return parseInt(product.id) === storageSKUID;
    })[0];

    const totalSKUsInOrderForm = items.filter(
      product => !SKUS_TO_IGNORE.includes(parseInt(product.id))
    ).length;

    if (!orderInfo) return;

    let quantity = await this.getTotalStorageQuantity();

    quantity *= userSelectedStorageMonths;

    const { user_need_storage } = orderInfo.fields;

    let isQuantityDifferent;

    if (storageProduct) {
      isQuantityDifferent = storageProduct.quantity !== quantity;
    }

    if (isQuantityDifferent && totalSKUsInOrderForm > 0) {
      return await this.cartService.updateProduct(storageSKUID, quantity);
    } else if (
      user_need_storage.toLowerCase() === 'true' &&
      totalSKUsInOrderForm > 0
    ) {
      return await this.addSKUStorageToOrderForm(storageSKUID, quantity);
    } else {
      return await this.removeSKUStorageInOrderForm(storageSKUID, items);
    }
  }
}
