export default class InsuranceService {
  constructor() {}

  async updateInsuranceSKUPrice(price, skuIndex, orderFormId) {
    if (skuIndex === -1 || !price) return;

    const endpoint = `https://wcdlfckgi9.execute-api.us-east-2.amazonaws.com/v1/api/checkout/pub/orderform/${orderFormId}/items/${skuIndex}/price`;

    return await (
      await fetch(endpoint, {
        method: 'PUT',
        body: JSON.stringify({
          price,
        }),
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
    ).json();
  }
}
